body {
  background-color: var(--background-white-100);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Nunito, Arial, sans-serif;
  display: flex;
}

[data-theme="dark"] {
  --background-white-100: #070707;
  --background-white-0: #000;
  --background-white-200: #111;
  --background-white-300: #383838;
  --background-white-400: #383838;
  --background-black-200: #e7e7e7;
  --border-color-100: #2e2e2e;
  --border-color-200: #111;
  --border-color-300: #4b4a4a;
  --border-color-400: #353535;
  --border-color-500: #4e4e4e;
  --text-color-100: #fff;
  --inverted-text-color-100: #000;
  --disabled-badge: #070707;
}

[data-theme="light"] {
  --background-white-100: #f3f2f2;
  --background-white-0: #fff;
  --background-white-200: #f3f3f3;
  --background-white-300: #ccc;
  --background-white-400: #fff;
  --background-black-200: #232323;
  --border-color-100: #e2e2e2;
  --border-color-200: #f3f3f3;
  --border-color-300: #c7c7c7;
  --border-color-400: #bebebe;
  --border-color-500: #e2e2e2;
  --text-color-100: #000;
  --inverted-text-color-100: #000;
  --disabled-badge: #858585;
}

